class Player {
	constructor(cam, characterClass) { 
	    // essentials
	    this.cam = cam;
	    this.characterProfile = [];
	    this.characterClass = characterClass;
	    
	    // game properties
	    this.mouseMode = 'split'; // 'split' or 'character'
	    this.pointingLeft = false;

	    // inventory
	    this.inventory = {};

	    // initialize quests
	    this.initQuests();

	    // gui
	    this.gui = new GUI();

	    // number style
	    this.numberColor = color(255);
	    this.numOffsetY = 70;
	    this.numOffsetX = 0;
		this.drawNumberBelow = true;

	    // show special number?
	    this.specialNumber = false;
	    this.specialLily = 0;
	    this.sizeSpecial = 40;
	    this.specialOffX = 30;
	    this.specialOffY = 80;

	    // fade screen
	    this.isFading = false;
	    this.fadeFrames = 0;
	    this.fadeTime = 3;
	}

	get hasQuest() {
		return this.currentQuest > -1;
	}

	instantiateCharacter(pushToIndex = 0) {
		this.character = new this.characterClass(...this.characterProfile);
		this.character.scale = this.cam.scale;
		frogs[pushToIndex] = this.character;

    // number binds
    this.binds = {
    	'Q': this.character.typeBind(1),
    	'W': this.character.typeBind(2),
    	'E': this.character.typeBind(3),
    	'R': this.character.typeBind(4)
    }

		return this.character;
	}

	resetCharacter() {
		this.instantiateCharacter();
		this.initQuests();

		this.cam.target = this.character;
	}

	assignQuest(quest) {
		this.questList.push(new quest(player));
  		this.currentQuest = 0;
	}

	initQuests() {
	    this.questList = [];
	    this.currentQuest = -1; // -1 is no quest
	    this.questDecayFrames = 0;
	    this.maxQuestFrames = 100;
	}

	/* frame */

	frame() {
		const charCamCoords = this.cam.toCamCoords(this.character.posX, this.character.posY);
		
		this.pointingLeft = (mouseX < charCamCoords[0]); // (mouseX < screenSizeX / 2) || ;
		this.character.environment.envDependentFrame(this);

		if (this.hasQuest) {
			if (this.questList[this.currentQuest].frame()) {
				// if success, fade out quest window
				this.questDecayFrames++;
			}

			if (this.questDecayFrames > 0) this.questDecayFrames++;

			if (this.questDecayFrames > this.maxQuestFrames) {
				this.questDecayFrames = 0;
				this.questList.slice(this.currentQuest, 1);
				this.currentQuest = -1;
			}
		}

		// reset?
		if (this.character.tooHungry && !this.isFading) {
			controlsEnabled = false;
			this.cam.guiOptions.helpLines = false;

			this.isFading = true;

		    // bugs
		    this.bugsN = 4;
		    this.bugsX = Array(this.bugsN).fill(0);
		    this.bugsY = Array(this.bugsN).fill(0);
		    this.bugsTX = Array(this.bugsN).fill(1);
		    this.bugsTY = Array(this.bugsN).fill(-1);
		    this.beeSprite = Array(this.bugsN);

		    for (let i = 0; i < this.bugsN; i++) {
		      this.bugsX[i] = Math.floor(Math.random() * screenSizeX);
		      this.bugsY[i] = Math.floor(Math.random() * screenSizeY);
		      this.bugsTX[i] = (Math.random() > .5) ? 1 : -1;
		      this.bugsTY[i] = (Math.random() > .5) ? 1 : -1;
		    }

		} if (this.isFading && this.fadeFrames < this.fadeTime) { 
			this.fadeFrames += deltaTime / 1000;
			this.character.posY += 10;
			this.quitReady = true;
		} 
		if (this.isFading) {
		    // update bugs
		    for (let i = 0; i < this.bugsN; i++) {
		      this.bugsX[i] += this.bugsTX[i];
		      this.bugsY[i] += this.bugsTY[i];

		      if (this.bugsX[i] > screenSizeX) this.bugsTX[i] = -1;
		      else if (this.bugsX[i] < 0) this.bugsTX[i] = 1;

		      if (this.bugsY[i] > screenSizeY) this.bugsTY[i] = -1;
		      else if (this.bugsY[i] < 0) this.bugsTY[i] = 1;

		      let baseName = (Math.floor(frameCount / 60) % 2 == 0) ? 'smallBee' : 'smallBeeWing';
		      let appended = (this.bugsTX[i] == 1) ? 'Right' : 'Left';
		      this.beeSprite[i] = baseName + appended;
		    }
		}

		// server
		//if (this.character.isJumping) socket.emit('frame', [player.character.posX, player.character.posY]);
	}

	/* draw */

	drawHunger() {
		const camFrogPos = this.cam.toCamCoords(this.character.posX, this.character.posY);

	  const hungerBarWidth = gfx['flyBar'].width;
		const hungerBarHeight = gfx['flyBar'].height;
	  const hungerRatio = 1 - (frog.hunger / frog.maxHunger);

	  fill(0);
	  stroke(0);
	  // textSize(24);
	  // textFont(pixelFont);
	  // text('Hunger', screenSizeX - 1.3 * gfx['flyBar'].width, 25);

	  if (frog.hungry) {
	    push();
	    tint(255, 255 * Math.cos(2 * frameCount / 60));
	    image(gfx['flyBubble'], camFrogPos[0] + frog.dimX, camFrogPos[1] - frog.dimY,
	          gfx['flyBubble'].width * cam.scale, gfx['flyBubble'].height * cam.scale);
	    pop();
	  }

	  if (frog.ateThisFrame) {
	    frog.showHeart.push(1);
	  }

	  for (const heart in frog.showHeart) {
	    if (frog.showHeart[heart] > 0) {
	      frog.showHeart[heart]++;
	      image(gfx['heart'], camFrogPos[0] + frog.dimX, camFrogPos[1] - frog.dimY - frog.showHeart[heart] / 2,
	            gfx['heart'].width * cam.scale, gfx['heart'].height * cam.scale);
	      frog.showHeart[heart] %= frog.heartFrames;
	    } else {
	      frog.showHeart.splice(heart, 1);
	    }
	  }

	  push();
	  translate(screenSizeX - gfx['flyBar'].width - 5, 10);
	  fill(255);
	  stroke(0);
	  rect(-5, -10, hungerBarWidth + 10, hungerBarHeight + 20, 20);
	  image(gfx['flyBarGrey'], 0, 0);
	  if (hungerRatio * hungerBarWidth >= 1) {
		  const flyBar = gfx['flyBar'].get(0, 0, Math.floor(hungerRatio * hungerBarWidth), hungerBarHeight);
		  image(flyBar, 0, 0);
	  }
	  pop();
	}

	drawQuest(quest) {
	  const opacity = 255 * (1 - this.questDecayFrames / this.maxQuestFrames);
	  quest.gui.draw(opacity);

	  push();
	  quest.draw(gfx, opacity);
	  pop();
	}

	draw() {
		push();
		// main gui
		this.gui.draw();


	    /* ~ additional hunger handler ~ */
	    if (this.character.tooHungry) {
	    	let fadeOpacity = this.fadeFrames / this.fadeTime;
	     	background(0, 255 * fadeOpacity);

			push();
			translate(screenSizeX / 2, 0);
			let textCol = color(255, 255 * Math.pow(fadeOpacity, 8));
			fill(textCol);
			stroke(textCol);
			textSize(64);
			textAlign(CENTER);
			textFont(pixelFont);
			text('Oh dear! :(', 0, screenSizeY / 5);
			text('You\'re too hungry.', 0, screenSizeY / 3);
			textSize(32);
			text('Crab says it\'s okay! Try again later?', 0, screenSizeY / 2 + gfx.crab.height * 1.25)
			text('Click to restart the demo...', 0, screenSizeY - 32);
			tint(255, 255 * fadeOpacity);
			image(gfx.crab, -gfx.crab.width / 2, screenSizeY / 2);
			pop();

		    // bugs >-<
		    push();
		    if (this.fadeFrames < this.fadeTime) tint(255, 255 * fadeOpacity);
		    scale(.25);
		    for (let i = 0; i < this.bugsN; i++) {
		        image(gfx[this.beeSprite[i]], this.bugsX[i] / .25, this.bugsY[i] / .25);
		    }
		    pop();
		}

		// hunger gui
		this.drawHunger();

		// quest gui
		if (this.currentQuest > -1) {
			this.drawQuest(this.questList[this.currentQuest]);
		}

	  // frog gui
	  this.character.scale = this.cam.scale;

		// help lines
		if (this.cam.guiOptions.helpLines && lilies.frogWithinBounds() && this.character.type != 0) {
		  if (!this.character.isJumping || (puzzleMode && !buttonPressed)) {
			let pointDir, extra;
			if (envType == 'CyclicLilies') { 
				pointDir = 1;
				extra = this.character.dimY / 2 - this.character.offY;
				
			} else {
				pointDir = this.character.pointingLeft ? -1 : 1;
				extra = 0;
			}

		    let startPosX = this.character.posX + this.character.dimX / 2;
		    let jumpDest = lilies.getLilyPos(this.character.lily + pointDir * this.character.type);
		    let jumpDestX = jumpDest[0] + this.character.dimX / 2 + this.character.offX;		    
		    let jumpDestY = jumpDest[1] + this.character.offY;
			let heightLevel = (this.character.posY < jumpDestY) ? this.character.posY : jumpDestY;

		    this.curveParams = [startPosX, this.character.posY + this.character.dimY / 2,
		        (startPosX + jumpDestX) / 2, heightLevel - this.character.jumpHeight + this.character.dimY / 2 + extra, 
		        jumpDestX, jumpDestY + this.character.dimY]
		  }

		  const camCurveParams = [];

		  for (let k = 0; k < 3; k++) {
		    let pt = [this.curveParams[2 * k], this.curveParams[2 * k + 1]];

		    [camCurveParams[2 * k], camCurveParams[2 * k + 1]] = this.cam.toCamCoords(...pt);
		  }

		  const allCurves = [camCurveParams];

		  push();
		  setLineDash([5, 5]);
		  stroke(255, 128);
		  strokeWeight(3);
		  fill(0, 0, 0, 0);

		  let drawDot = true;
		  
		  for (const params of allCurves) {
		    switch (envType) {
		      case 'LinearLilies':
		        beginShape();
		        curveVertex(params[0], params[1]);
		        curveVertex(params[0], params[1]);
		        curveVertex(params[2], params[3]);
		        curveVertex(params[4], params[5]);
		        curveVertex(params[4], params[5]);
		        endShape();

		        break;
		      case 'CyclicLilies':
				beginShape();
				curveVertex(params[0], params[1]);
				curveVertex(params[0], params[1]);
				curveVertex(params[2], params[3]);
				curveVertex(params[4], params[5]);
				curveVertex(params[4], params[5]);
				endShape();
			//   if (!this.character.isJumping) { 

		    //     //   line(params[0], params[1], params[4], params[5]);
		    //     } else drawDot = false;
		    }

		    if (drawDot) {
		      strokeWeight(7 * this.cam.scale);
		      point(params[4], params[5]);
		    }
		  }

		  pop();
		}

		// number
		if (!this.character.tooHungry) {
			push();

			if (this.character.changeNumber) {
			    this.numText = str(this.character.number);
			    this.lilyCamDiff = this.character.lilyPos;
			    this.lilyCamDiff[0] += this.character.dimX / 2 + this.numOffsetX;
			    this.character.changeNumber = false;
			}

			textFont(pixelFont);
			stroke(this.numberColor);
			fill(this.numberColor);
			textSize(this.sizeSpecial * this.cam.scale * 0.9);
			push();
			textAlign(CENTER);
			if (this.drawNumberBelow) text(this.numText, ...this.cam.toCamCoords(this.lilyCamDiff[0], this.lilyCamDiff[1] + this.numOffsetY * this.cam.scale));
			pop();

			if (this.specialNumber) {
				let specialPos = this.cam.toCamCoords(...this.character.environment.getLilyPos(this.specialLily));
				specialPos[0] += this.specialOffX - 2 * this.specialLily.toString().length;
				specialPos[1] += this.specialOffY * cam.scale;

				text(this.specialLily.toString(), ...specialPos);
			}

			pop();
		}
	}
}